import React, { useState, useEffect } from 'react';
import { Tabs, Button } from 'antd';
import CreateRoomTypeInfo from './CreateRoomTypeTab/CreateRoomTypeInfo';
import CreateRoomTypeAmenities from './CreateRoomTypeTab/CreateRoomTypeAmenities';
import CreateRoomTypeImages from './CreateRoomTypeTab/CreateRoomTypeImages';
const TabPane = Tabs.TabPane;

const routes = [
	{
		title: 'Thông tin phòng',
		component: CreateRoomTypeInfo
	},
	{
		title: 'Tiện nghi phòng',
		component: CreateRoomTypeAmenities
	},
	{
		title: 'Hình ảnh phòng',
		component: CreateRoomTypeImages
	}
]

export default function CreateRoomType(props) {
	const [roomId, setRoomId] = useState(undefined);

	useEffect(() => {
		console.log(props);
	}, [])

	const onCreateRoom = (id) => {
		setRoomId(id);
	}

	return (
		<div>
			<Button onClick={() => props.history.goBack()} icon='left'>
				Quay lại
			</Button>
			<Tabs defaultActiveKey='0'>
				{
					routes.map((child, index) => {
						return (
							<TabPane tab={child.title} key={index} disabled={index !== 0 && !roomId}>
								<child.component {...props} roomId={roomId} onCreateRoom={onCreateRoom} />
							</TabPane>
						)
					})
				}
			</Tabs>
		</div>
	)
}
