import React, { useState, useEffect } from 'react';
import { getAllAmenity } from '../../../api/AmenityApi';
import { updateAmenities } from '../../../api/RoomTypeApi';
import { Tabs, Checkbox, Row, Col, Button, message } from 'antd';
import { parseLanguage } from '../../../helpers/LanguageHelper';

const TabPane = Tabs.TabPane;

export default function CreateRoomTypeAmenities(props) {
	const [amenities, setAmenities] = useState([]);
	const [groupName, setGroupName] = useState([]);
	const [checkedAmenities, setCheckedAmenities] = useState([]);

	useEffect(() => {
		if (props.roomId) {
			fetchRoomTypeAmenities(props.roomId);
		}
	}, [props.roomId])

	const fetchRoomTypeAmenities = async (id) => {
		getAllAmenity({ instanceId: id, amenityType: 'room_amenity' }).then(res => {
			if (res.status === 'SUCCESS' && res.value) {
				console.log(res.value);
				res.value[0].amenities && setAmenities(res.value[0].amenities);
				res.value[0].i18ns && setGroupName(res.value[0].i18ns);
				const checkedArr = res.value[0].amenities.map(item => {
					if (item.checked) return item.amenityId;
				});
				setCheckedAmenities(checkedArr);
			}
		})
	}

	const onChange = (checkedValues) => {
		setCheckedAmenities(checkedValues);
	}

	const onSaveBtnClick = () => {
		if (!props.hotelId || !props.roomId) return;
		updateAmenities({ hotelId: props.hotelId, roomId: props.roomId, amenityIds: checkedAmenities }).then(res => {
			if (res.status === 'SUCCESS') {
				message.success('Cập nhật tiện ích thành công');
			} else {
				message.error('Cập nhật tiện ích không thành công');
			}
		}).catch(err => message.error('Cập nhật tiện ích không thành công'))
	}

	return (
		<div>
			<Tabs defaultActiveKey='0'>
				{
					groupName.map((item, index) => {
						return (
							<TabPane tab={parseLanguage(item.lang)} key={index}>
								<b>{item.groupName}</b>
								<Checkbox.Group style={{ width: '100%' }} onChange={onChange}
									value={[...checkedAmenities]}
								>
									<Row>
										{
											amenities.map((amenity, idx) => {
												return (
													<Col span={8} key={idx}>
														<Checkbox value={amenity.amenityId}>
															{amenity.i18ns[index].title}
														</Checkbox>
													</Col>
												)
											})
										}
									</Row>
								</Checkbox.Group>
							</TabPane>
						)
					})
				}
			</Tabs>
			<Button type='primary' onClick={onSaveBtnClick} style={{ marginTop: '15px' }}
				disabled={!amenities || amenities.length === 0}
			>
				Lưu
      </Button>
		</div>
	)
}
