import { post } from './ApiSender';
import { RoomTypeApi } from './constant';

export function createRoomType(payload) {
    return post(RoomTypeApi.createRoomType, payload);
}

export function updateRoomType(payload) {
    return post(RoomTypeApi.updateRoomType, payload);
}

export function updateAmenities(payload) {
    return post(RoomTypeApi.updateAmenities, payload);
}

export function getRoomTypeById(payload) {
    return post(RoomTypeApi.getRoomTypeById, payload);
}
